import { connect } from 'react-redux';
import { reduxForm, formValueSelector } from 'redux-form';
import SignupFormComponent from './SignupFormComponent';
import { getQueryParam } from '../../helpers/queryParamsHelper';
import { signup, clearSignupError } from '../../redux/actions/signupActions';

const SignupFormForm = reduxForm({
  form: 'signup',
  onSubmit: (payload, dispatch, ownProps) => {
    if (window.location.search) {
      payload.AffiliateTag = getQueryParam('aff');
    } else {
      payload.AffiliateTag = window.localStorage.getItem('affiliateTag');
    }
    dispatch(signup(payload));
  }
})(SignupFormComponent);

const selector = formValueSelector('signup');

const mapStateToProps = state => {
  return {
    passwordValue: selector(state, 'password'),
    errorMsg: state.signup.errorMsg,
    done: !!state.signup.userId,
    isAuthenticated: state.auth.isAuthenticated
  };
};

const mapDispatchToProps = {
  clearSignupError
};

const SignupFormContainer = connect(
  mapStateToProps,
  mapDispatchToProps
)(SignupFormForm);

export default SignupFormContainer;



