import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Redirect, Link } from 'react-router-dom';
import APInput from '../common/APInput/APInput';
import APPasswordInput from '../common/APPasswordInput/APPasswordInput';
import APButton from '../common/APButton';
import {
  required,
  email,
  matchingPassword,
  validatePassword
} from '../../helpers/formValidations';
import { renderFormInputs } from '../../helpers/formGeneratorHelper';
import path from '../../helpers/path';
import passwordScore from '../../helpers/passwordScore';
import { getBEMClasses } from '../../helpers/cssClassesHelper';
import { defaultPath } from '../../routeTemplates';
import config from '../../config';
import { styleNames } from '../../propTypes/commonComponent';

import '../../styles/components/common/StandaloneForm.css';
import './SignupFormComponent.css';

const baseClasses = getBEMClasses('standalone-form');
const signupFormClasses = getBEMClasses('signup-form');

class SignupFormComponent extends Component {
  getPasswordStrength = () => {
    const { context } = this;
    const score = passwordScore(this.props.passwordValue);
    const passwordStrengthValues = {
      0: context.t('Very Weak'),
      1: context.t('Weak'),
      2: context.t('Medium'),
      3: context.t('Strong'),
      4: context.t('Very Strong')
    };

    return passwordStrengthValues[score];
  };

  handleChange = () => {
    if (this.props.errorMsg) {
      this.props.clearSignupError();
    }
  };

  render() {
    const { context } = this;
    const {
      handleSubmit,
      errorMsg,
      submitting,
      pristine,
      isAuthenticated,
      passwordValue,
      invalid,
      done
    } = this.props;

    const {
      useEmailAsUsername,
      additionalFields,
      termsAndServicesLink,
      privacyPolicyLink
    } = config.SignupForm;

    const termsLink = (
      <Link to={path(termsAndServicesLink)} target="_blank">
        {context.t('Terms and Conditions')}
      </Link>
    );
    const privacyLink = (
      <Link to={path(privacyPolicyLink)} target="_blank">
        {context.t('Privacy Policy')}
      </Link>
    );

    if (isAuthenticated) {
      return <Redirect to={path(`${defaultPath.path}`)} />;
    }

    return (
      <div
        className={`${baseClasses('container')} ${signupFormClasses(
          'form-container'
        )}`}>
        <div
          className={`${baseClasses('header')} ${signupFormClasses('header')}`}>
          <div
            className={`${baseClasses('header-text')} ${signupFormClasses(
              'header-text'
            )}`}>
            {done
              ? context.t('Signup success')
              : context.t('{n} Sign Up', { n: config.global.siteName })}
          </div>
        </div>
        {done ? (
          <p className={`${baseClasses('success')}`}>
            {context.t(
              'Thanks for signing up, please check your inbox for a confirmation email'
            )}
          </p>
        ) : (
          <form
            onSubmit={handleSubmit}
            className={`${baseClasses('form')} ${signupFormClasses('form')}`}>
            <div className={signupFormClasses('wrapper')}>
              <div className={signupFormClasses('container')}>
                {!useEmailAsUsername && (
                  <APInput
                    type="text"
                    name="username"
                    label={context.t('Username')}
                    validate={[required]}
                    customClass={signupFormClasses()}
                    onChange={this.handleChange}
                  />
                )}
                {errorMsg && (
                  <p
                    className={`${baseClasses('error')} ${signupFormClasses(
                      'error'
                    )}`}>
                    {errorMsg}
                  </p>
                )}
                <APInput
                  type="text"
                  name="email"
                  label={context.t('Email')}
                  validate={[required, email]}
                  customClass={signupFormClasses()}
                  onChange={this.handleChange}
                />
                {renderFormInputs(
                  additionalFields,
                  signupFormClasses(),
                  context
                )}
              </div>
              <div className={signupFormClasses('container')}>
                <APPasswordInput
                  type="password"
                  name="password"
                  label={context.t('Password')}
                  validate={[required]}
                  info={passwordValue && this.getPasswordStrength()}
                  customClass={signupFormClasses()}
                  onChange={this.handleChange}
                />
                <APPasswordInput
                  type="password"
                  name="matchingPassword"
                  label={context.t('Retype Password')}
                  classes="signup"
                  validate={[required, matchingPassword, validatePassword]}
                  customClass={signupFormClasses()}
                  onChange={this.handleChange}
                />
              </div>
            </div>

            <div className={signupFormClasses('footer')}>
              <APButton
                type="submit"
                disabled={pristine || submitting || invalid}
                customClass={`${baseClasses()} ${signupFormClasses()}`}
                styleName={styleNames.additive}>
                {submitting ? context.t('Processing...') : context.t('Sign Up')}
              </APButton>

              <div className={signupFormClasses('text')}>
                {context.t('By Clicking')}
                <span className={signupFormClasses('bold-text')}>
                  {context.t(' Sign up ')}
                </span>
                {context.t('you accept our {t} and {p}', {
                  t: termsLink,
                  p: privacyLink
                })}
              </div>
            </div>
          </form>
        )}

        <div
          className={`${baseClasses('footer')} ${signupFormClasses('links')}`}>
          <Link to={path('/login')} className={signupFormClasses('link')}>
            {context.t('Login')}
          </Link>
          <div className={`${baseClasses('footer-separator')}`} />
          <Link
            to={path('/problem-logging-in')}
            className={signupFormClasses('link')}
            disabled>
            {context.t('Forgot Password?')}
          </Link>
        </div>
      </div>
    );
  }
}

SignupFormComponent.defaultProps = {
  handleSubmit: () => {},
  submitting: false
};

SignupFormComponent.propTypes = {
  handleSubmit: PropTypes.func,
  submitting: PropTypes.bool,
  referrer: PropTypes.string,
  search: PropTypes.string,
  shouldRedirect: PropTypes.bool,
  isAuthenticated: PropTypes.bool
};

SignupFormComponent.contextTypes = {
  t: PropTypes.func.isRequired
};

export default SignupFormComponent;



